.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.lcbo-card{
  max-width: 340px;
}
.lcbo-card-media{
  height: 340px;
}
.root{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  overflow: hidden;
}
.cardGrid{
  margin: 84px 10px 10px 10px;
}
.gridList{
  width: 500;
  height: 450;
}
.icon{
  color: rgba(255, 255, 255, 0.54);
}
.layout{
  width: auto;
}

.card{
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.cardMedia{
  padding-top: 56.25%;
}
.cardContent{
  -ms-flex-positive: 1;
      flex-grow: 1;
}
.progress-bar{
  margin-top: 64px;
}
.formControl{
  min-width: 120px !important;
  height: 64px;
}
.badge{
  padding-right: 14px;
  -ms-flex-positive: 1;
      flex-grow: 1;
  text-align: end;
  display: inline-block !important;
}
.dense{
  margin-right: 16px !important;
}


